@import "fonts";
@import "variables";
@import "mixins";

html,
body {
  font-size: 16px;
}

div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  font-family: "Rosatom", "Arial Black";
  color: $primaryFC;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
//body {
//  line-height: 1;
//}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
* {
  box-sizing: border-box;
}

.wrapper {
  max-width: 1350px;
  margin: 0 auto;
}

.header {
  height: 101px;
  border-bottom: 1px solid $borderColorFirst;

  &__wrapper {
    display: flex;
    align-items: center;
    height: 100%;
  }

  &__logo {
    position: relative;
    width: 240px;
    height: 100px;
    & a {
      display: block;
      width: 100%;
      height: 100%;
    }
  }

  &__nav {
    display: flex;
    justify-content: space-between;
    height: 100%;
  }
  &__nav-left {
    height: 100%;
    display: flex;
  }
  &__nav-item {
    display: flex;
    height: 100%;
    align-items: center;
    margin: 0 10px;
    & a {
      display: flex;
      align-items: center;
      width: 100%;
      height: 100%;
      padding: 0 20px;
      font-size: 14px;
      text-transform: uppercase;
      color: $primaryFC;
      text-decoration: none;
      font-weight: bold;
      transition: 0.3s ease-in-out;
      &:hover {
        text-decoration: none;
        color: $thirdFC;
      }
    }
    &.active {
      & a {
        color: $thirdFC;
      }
    }
    &--light {
      & a {
        padding-left: 5px;
        font-weight: 300;
        text-transform: none;
      }
    }
  }
}

.avatar {
  width: 50px;
  height: 50px;
  background-image: url("https://cdn4.iconfinder.com/data/icons/ionicons/512/icon-image-512.png");
  background-position: center;
  border-radius: 50%;
  background-size: 20px;
  background-repeat: no-repeat;
  border: 1px solid $borderColorFirst;
  & a {
    display: block;
    width: 100%;
    height: 100%;
  }
}
.header__burg {
  display: none;
}
.header__hide {
  display: flex;
  margin-left: 25px;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  height: 100%;
}
.header__nav {
  align-items: center;
  & ul {
    margin-bottom: 0;
  }
}
.header__right {
  display: flex;
  align-items: center;
}
.header__burg {
  & span {
    display: block;
    width: 33px;
    height: 4px;
    margin-bottom: 5px;
    position: relative;
    background: #333333;
    border-radius: 3px;
    z-index: 1;
    transform-origin: 4px 0px;
    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),
      background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;
  }
  & span:first-child {
    transform-origin: 0% 0%;
  }
  & span:nth-last-child(2) {
    transform-origin: 0% 100%;
  }
}

@include mxm(1350) {
  .wrapper {
    width: 100%;
  }
}
.header__exit {
  display: none;
  margin-left: 25px;
}
.header__avatar-wrapper {
  & p {
    display: none;
  }
}

@include mxm(1120) {
  .header__hide {
    display: flex;
    flex-direction: column;
  }
  .header__wrapper {
    background-color: white;
    position: relative;
    z-index: 10;
  }
  .header__hide {
    top: -500px;
    position: absolute;
    left: 0;
    margin-left: 0;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    z-index: 1;
    transition: 0.3s ease-in-out;
    flex-direction: column;
    background-color: white;
    &.active {
      padding-top: 10px;
      top: 100px;
    }
    height: auto;

    padding-bottom: 10px;
    border-bottom: 1px solid $borderColorFirst;
  }
  .header__right {
    order: 0;
    margin-left: 20px;
  }
  .header__nav {
    order: 1;
  }
  .header__logo {
    position: relative;
    z-index: 11;
  }
  .header__burg {
    position: relative;
    z-index: 11;
    padding: 5px;
    padding-bottom: 1px;
    border: 1px solid transparent;
    &.active {
      border: 1px solid $borderColorFirst;
      border-radius: 3px;
      transition: 0.3s ease;
    }
  }
  .header__layer {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background-color: white;
    z-index: 10;
  }
  .header__nav-left {
    display: flex;
    flex-direction: column;
  }

  .header__burg {
    display: block;
    margin-right: 10px;
  }
  .header__wrapper {
    justify-content: space-between;
  }
  .header__nav-item {
    height: 35px;
  }
  .header__nav-right {
    display: none;
  }
  .header__exit {
    display: block;
  }
  .header__avatar-wrapper {
    display: flex;
    align-items: center;
    & p {
      display: block;
      margin-bottom: 0;
      font-weight: bold;
      margin-left: 10px;
    }
  }
}
.header {
  margin-bottom: 50px;
}
.content {
  border: 1px solid $borderColorFirst;
  border-top: none;
  border-radius: 5px;
  border-top-right-radius: 0;
  border-top-left-radius: 0;
  min-height: 200px;
}
.nav-tabs-custom {
  & li {
    &.active {
      & * {
        color: $thirdFC !important;
      }
    }
  }

  & * {
    font-weight: bold;
    color: $primaryFC;

    &:hover {
      color: $secondaryFC;
    }
  }
}

.header__enter {
  position: absolute;
  right: 0;
  height: 100px;
}
.content {
  padding: 20px;
  &__top {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__name {
    font-size: 22px;
    font-weight: bold;
  }
  &__date {
    align-items: center;
    display: flex;
    & p {
      font-size: 16px;
      margin-bottom: 0;
    }
    & span {
      text-align: center;
      font-size: 12px;
      opacity: 0.5;
    }
    & div {
      flex-direction: column;
      display: flex;
      margin-left: 10px;
    }
    &-label {
      font-size: 12px;
      font-weight: 300;
      display: none;
    }
    &-label-preview {
      font-size: 12px;
      font-weight: 300;
    }
    @include mxm(500) {
      &-label {
        display: block;
      }
    }
  }
  @include mxm(500) {
    &__top {
      display: flex;
      justify-content: center;
    }
  }
  &__button-wrapper {
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
    & .myButton {
      margin-bottom: 5px;
      margin-right: 5px;
      &:last-child {
        margin-right: 0;
      }
    }
  }
}
.header__avatar {
  overflow: hidden;
  & a {
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: block;
    & img {
      width: 100%;
      object-fit: cover;
      height: 100%;
    }
  }
}
.myButton {
  padding: 5px 15px;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: bold;
  border: 1px solid $secondaryFC;
  background-color: transparent;
  transition: 0.3s ease;
  color: $secondaryFC;
  &:hover {
    border: 1px solid $primaryFC;
    color: $primaryFC;
    text-decoration: none;
  }
  &--green {
    border-color: $green;
    color: $green;
    &:hover {
      border-color: $greenEffect;
      color: $greenEffect;
    }
  }
  &--red {
    border-color: $red;
    color: $red;
    &:hover {
      border-color: $redEffect;
      color: $redEffect;
    }
  }
  &--white {
    border-color: white;
    border-radius: 5px;
    color: white;
    &:hover {
      border-color: #efefef;
      color: #efefef;
    }
  }
  &--blue {
    border-color: $blue;
    color: $blue;
    &:hover {
      border-color: $blueEffect;
      color: $blueEffect;
    }
  }
}
.content__item {
  margin-bottom: 10px;
}

.price {
  display: flex;
  align-items: center;
  font-size: 18px;
  font-weight: 300;
  & span {
    font-weight: normal;
  }
}
.content {
  &__info {
    & header {
      font-size: 18px;
      margin-bottom: 5px;
    }
  }
  &__infoMain {
    border-radius: 5px;
    border: 1px solid $borderColorFirst;
    padding: 10px;
    * {
      margin-bottom: 8px;
    }
    ul {
      li {
        list-style: disc;
        list-style-position: inside;
        margin-bottom: 0px;
      }
    }
  }
}
.myGridProject {
  width: 100%;
  min-height: 500px;
  display: grid;
  grid-auto-rows: minmax(320px, auto);
  grid-auto-columns: minmax(320px, 480px);
  grid-template-columns: 420px 420px 420px;
  justify-content: space-around;
  grid-row-gap: 20px;
  flex-wrap: wrap;
  & > div {
    width: 100%;
    height: auto;
    padding: 5px;
  }
}
@include mxm(1260) {
  .myGridProject {
    width: 100%;
    min-height: 500px;
    display: grid;
    grid-auto-rows: minmax(320px, auto);
    grid-auto-columns: 320px;
    justify-content: space-around;
    grid-template-columns: minmax(320px, 420px) minmax(320px, 420px) minmax(
        320px,
        420px
      );
    grid-row-gap: 20px;
    flex-wrap: wrap;
    & > div {
      width: 100%;
      height: auto;
      padding: 5px;
    }
  }
}
@include mxm(1000) {
  .myGridProject {
    width: 100%;
    min-height: 500px;
    display: grid;
    grid-auto-rows: auto;
    grid-template-columns: 320px 320px;
    grid-auto-columns: 320px;
    justify-content: space-around;

    flex-wrap: wrap;
    & > div {
      width: 100%;
      height: auto;
      padding: 5px;
    }
  }
}
@include mxm(680) {
  .myGridProject {
    width: 100%;
    min-height: 500px;
    display: grid;
    grid-auto-rows: auto;
    grid-template-columns: 320px;
    grid-auto-columns: 320px;
    justify-content: center;

    flex-wrap: wrap;
    & > div {
      width: 100%;
      height: auto;
      padding: 5px !important;
    }
  }
}
.card {
  display: flex;
  flex-direction: column;
  & a {
    //color: $thirdFC;
    transition: 0.3s ease;
    &:hover {
      text-decoration: none;
    }
  }
  &__label {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 0;
  }
  &__info {
    &--big {
      * {
        font-size: 16px !important;
      }
    }
  }
  &__notHEAD {
    position: static;
    top: 0 !important;
  }
  &__head {
    background-color: #808080;
    border-radius: 10px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    height: 70px;
    padding: 10px 20px !important;
    padding-bottom: 20px !important;
    display: flex;
    justify-content: space-between;
  }
  &__head-item {
    display: flex;
    flex-direction: column;
    & span:first-child {
      color: white;
      font-weight: bold;
      font-size: 12px;
    }
    & span:last-child {
      font-weight: bold;
      font-size: 10px;
    }
  }
  &__body {
    background-color: #f2f2f2;
    border-radius: 10px;
    min-height: 320px;
    position: relative;
    display: flex;
    flex-direction: column;
    top: -20px;
    padding: 20px;
  }
  &__info {
    margin-bottom: 5px;
    display: flex;
    flex-direction: column;
    & p {
      margin-bottom: 0 !important;
    }
  }
  &__wrapperAvatar {
    display: flex;
    align-items: center;
  }
  &__avatar {
    border: 10px;
    height: 200px;
    width: 100%;
    padding: 10px 10px !important;
    background-color: #dedede;
    overflow: hidden;
    align-self: flex-end;
    & img {
      object-fit: contain;
      width: 100%;
      height: 100%;
    }
    @include mxm(1000) {
      height: 150px;
    }
  }
  &__label {
    margin-top: 10px !important;
  }
  &__additional {
    display: flex;
    align-items: center;
    margin-bottom: 3px !important;
    font-size: 12px;
    font-weight: 300;
    & span {
      font-weight: normal;
      font-size: 12px;
    }
  }
  &__body > div:first-child {
    & p:first-child {
      margin-top: 0 !important;
    }
  }
  &__files {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 0 20px;
    margin-top: 10px;
    flex-wrap: wrap;
  }
  &__file {
    margin-bottom: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: $red !important;
    &:hover {
      text-decoration: none;
      cursor: pointer;
    }
    & span:first-child {
      opacity: 0.8;
    }
    & span:last-child {
      opacity: 0.7;
      max-width: 100%;
      white-space: nowrap; /* Запрещаем перенос строк */
      overflow: hidden; /* Обрезаем все, что не помещается в область */
      text-overflow: ellipsis;
    }
    max-width: 100px;
  }
  &__textWrapper {
    & > p {
      font-size: 14px;
      margin-top: 6px !important;
      margin-bottom: 3px !important;
    }
    & > div {
      border-radius: 10px;
      border: 1px solid $borderColorFirst;
      padding: 5px !important;
      & p {
        margin-bottom: 0 !important;
        font-size: 12px;
      }
    }
  }
}
.card__buttons {
  margin-top: auto;
  padding-top: 10px;
  display: flex;
  justify-content: space-between;
  & a {
    //color: inherit!important;
    cursor: pointer;
  }
}
.card__additional {
  & .star {
    & img {
      width: 15px;
      height: 15px;
      position: absolute;
      left: 50%;
      top: 45%;
      transform: translate(-50%, -50%);
    }
    width: 20px;
    height: 20px;
    position: relative;
    background-repeat: no-repeat;
    background-position: center;
    animation: rotateStar 10s linear infinite;
  }
}

@keyframes rotateStar {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.card__head--center {
  display: flex;
  align-items: center;
  justify-content: center;
  & span {
    font-size: 15px;
    position: relative;
    top: -2px;
    text-transform: uppercase;
    font-weight: 300 !important;
  }
}
.card__to {
  display: flex;
  & span:first-child {
    font-size: 14px;
  }
}
.card__info {
  & a {
    color: #1c75dd;
    &:hover {
      color: #333333;
    }
  }
}
.card__label {
  & a {
    color: #1c75dd;
    &:hover {
      color: #333333;
    }
  }
}
.card__textWrapper {
  & a {
    color: #1c75dd;
    &:hover {
      color: #333333;
    }
  }
}
.not__margin {
  margin: 0;
}
.content__top {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.content__line {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.content__avatar-wrapper {
  width: 320px;
  background-color: #dedede;
  display: flex;
  align-items: center;
  justify-content: center;
  & img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}
.myPreview {
  display: flex;
  flex-direction: column;
  &__myCounter {
    margin-bottom: 20px;
  }
  &__header{
    font-size: 42px;
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
    margin-bottom: 10px;
    display: none;
  }
}
.myCounter {
  height: 100px;
  opacity: 0;
  background: rgb(21, 37, 109);
  background: linear-gradient(
    90deg,
    rgba(21, 37, 109, 1) 0%,
    rgba(68, 149, 209, 1) 100%
  );
  &__buttonWrapper{
    position: relative;
    display: flex;
    justify-content: flex-end;
  }
  &__myButton{
    position: absolute; left: -250px;
    top: 50%;
    transform: translateY(-50%);
  }

  &--rev {
    align-self: flex-end;
    background: linear-gradient(
      90deg,
      rgba(68, 149, 209, 1) 0%,
      rgba(21, 37, 109, 1) 100%
    );
    justify-content: flex-start;
  }
  &--65 {
    width: 65%;
  }
  &--80 {
    width: 80%;
  }
  &--60{
    width: 60%;
  }
  &--50 {
    width: 50%;
  }
  &--75{
    width: 75%;
  }
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 50px;
}

.MyCounter__textWrapper{
  font-size: 27px;
  font-weight: bold;
  color: white;
  transition: 0.3s ease;
  & a{
    color: white;
    &:hover{
      color: white;
    }
  }
}

.animShowUpToBottom{
  animation: ShowUpToBottom 1s ease-in-out forwards;
}
.animRightToLeft{
  animation: RightToLeft 1.5s ease-in-out forwards;
}

.js-preview{
  overflow-x: hidden;
}

@keyframes ShowUpToBottom {
  0% {
    opacity: 0;
    transform: translateY(-100px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}

@keyframes RightToLeft {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.animShowOpacity{
  animation:  ShowOpacity 0.5s ease-in-out forwards;
}
.myCounter__myButton{
  opacity: 0;
}

@keyframes ShowOpacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.myButton--BIG{
  padding: 20px 25px;
}
.buttonPreview__line{
  display: none;
}
@include mxm(1000){
  .animShowUpToBottom{
    animation: none;
  }
  .animRightToLeft{
    animation: none;
  }
  .myCounter{
    min-width: 60%;
    opacity: 1;
    &__myButton{
      display: none;
      opacity: 1;
      right: 0;
      top: 100%;
      left: 50%;
      transform: translateX(-50%);
      position: absolute;
      max-width: 165px;
      max-height: 100px;
    }

  }
  .myPreview{
    margin-bottom: 50px;
  }
  .buttonPreview{
    //max-width: 275px;
    text-align: center;
  }
  .buttonPreview__line{
    display: flex;
    width: 100%;
    justify-content: center;
  }
}
@include mxm(600){
  .myCounter{
    width: 100%;
  }
}
@include mxm(550){
  .MyCounter__textWrapper{
    font-size: 22px;
    & a{
      color: white;
      &:hover{
        color: white;
      }
    }
  }
  .myPreview {
    &__header{
      font-size: 36px;
    }
  }
}